
"use client";

import { useState, useRef } from 'react';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Eye, Download } from 'lucide-react';
import ProjectReport from './ProjectReport';
import Performance from './Performance';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';

const DashboardWrapper = () => {
  const [role, setRole] = useState('CPC');
  const [dateRange, setDateRange] = useState('All');
  const [isPreviewOpen, setIsPreviewOpen] = useState(false);
  const contentRef = useRef(null);

  const handleDownloadPDF = () => {
    const input = contentRef.current;
    if (input) {
      html2canvas(input).then((canvas) => {
        const imgData = canvas.toDataURL('image/png');
        const pdf = new jsPDF();
        const imgProps = pdf.getImageProperties(imgData);
        const pdfWidth = pdf.internal.pageSize.getWidth();
        const pdfHeight = (imgProps.height * pdfWidth) / imgProps.width;
        pdf.addImage(imgData, 'PNG', 0, 0, pdfWidth, pdfHeight);
        pdf.save(`${role === 'CPC' ? 'project-report' : 'performance'}.pdf`);
        setIsPreviewOpen(false);
      });
    }
  };

  return (
    <div className="p-4 max-w-5xl mx-auto">
      <div className="flex justify-between items-center mb-4">
        <h1 className="text-xl font-bold">{role === 'CPC' ? 'Project Report' : 'My Performance'}</h1>
        <div className="flex gap-3">
          <Select value={role} onValueChange={setRole}>
            <SelectTrigger className="w-[140px]">
              <SelectValue placeholder="Select Role" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="CPC">CPC</SelectItem>
              <SelectItem value="Employee">Employee</SelectItem>
            </SelectContent>
          </Select>
          <Select value={dateRange} onValueChange={setDateRange}>
            <SelectTrigger className="w-[140px]">
              <SelectValue placeholder="Date Range" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="All">All Time</SelectItem>
              <SelectItem value="Last30">Last 30 Days</SelectItem>
              <SelectItem value="Last7">Last 7 Days</SelectItem>
            </SelectContent>
          </Select>
          <Dialog open={isPreviewOpen} onOpenChange={setIsPreviewOpen}>
            <DialogTrigger asChild>
              <Button>
                <Eye className="mr-2 h-4 w-4" /> Preview
              </Button>
            </DialogTrigger>
            <DialogContent className="max-w-4xl">
              <DialogHeader>
                <DialogTitle>{role === 'CPC' ? 'Report' : 'Performance'} Preview</DialogTitle>
              </DialogHeader>
              <div className="p-4 bg-white overflow-auto max-h-[80vh]">
                {role === 'CPC' ? (
                  <ProjectReport dateRange={dateRange} ref={contentRef} />
                ) : (
                  <Performance dateRange={dateRange} ref={contentRef} />
                )}
              </div>
              <Button onClick={handleDownloadPDF}>
                <Download className="mr-2 h-4 w-4" /> Download PDF
              </Button>
            </DialogContent>
          </Dialog>
        </div>
      </div>

      <div ref={contentRef}>
        {role === 'CPC' ? (
          <ProjectReport dateRange={dateRange} />
        ) : (
          <Performance dateRange={dateRange} />
        )}
      </div>
    </div>
  );
};

export default DashboardWrapper;